import common from '@/common/common.js'


export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userInfo:[],
			tabIndex: 0,
			couponList:[],
			
			couponQR:'',			//优惠券二维码
			showCouponQR:false,		//是否显示核销码
			
			page:0,
			isall:0,
		}
	},
	onLoad: function() {
		var that = this
		that.getUserCouponList()
		console.log("newsitems",this.newsitems)
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		
		that.resetAttr();
		that.getUserCouponList()
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	onReachBottom(){
		var that = this
		that.loadmore();
	},
	methods: {
		toggleCouponQR(){
			var that = this
			var showCouponQR = that.showCouponQR
			that.showCouponQR = !showCouponQR
		},
		//加载更多
		loadmore(e){
			var that = this
			that.getUserCouponList();
		},
		//优惠券核销码
		useCoupon(e){
			var that = this
			var id = e.currentTarget.dataset.id
			uni.request({
				url: common.baseUrl + 'sharepic/getCouponQR',
				method: 'GET',
				data: {
					id:id
				},
				success: res => {
					console.log("orderQR",res);
					that.couponQR = res.data.couponQR
					that.toggleCouponQR();
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.couponList = []
		},
		//获取用户优惠券列表
		getUserCouponList(){
			var that = this
			var uid = uni.getStorageSync('userid');
			uni.request({
				url: common.apiUrl + 'user/getUserCoupoList',
				method: 'GET',
				data: {
					userid:uid,
					type:that.tabIndex,
					page:that.page+1
				},
				success: res => {
					console.log(res.data.coupon_list)
					that.couponList = that.couponList.concat(res.data.coupon_list)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		changeTab(e){
			var that = this
			var tabIndex = e.currentTarget.dataset.tabindex
			console.log(e)
			if(that.tabIndex != tabIndex){
				that.tabIndex = tabIndex
				that.resetAttr();
				that.getUserCouponList();
			}
		}
		
	}
}
